.header-container{
    
    color: white;
    background-size: cover;
  background-position: center;
  position: relative;
}
.hero {
    background-image: url('https://www.pixelcrumb.com/wp-content/uploads/2016/12/ARCHER-BANNER.jpg');
    height: 500px;
    width: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  .hero::after {
    content: "";
    display: block;
    opacity: .4;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: black;
  }
  
  .hero * {
    position: relative;
    z-index: 2;
  }
  
  .hero h1, .hero h2 {
    color: white;
  }
  
  .hero h1 {
    font-size: 60px;
    padding-top: 140px;
    margin-top: 0;
  }
  